    }

    &__month-slider--1939 {
        justify-content: flex-end;

        input[type="range"] {
            width: 33.33%;
        }

        input[type="range"]::-webkit-slider-thumb {
            width: 25%;
        }
    }

    &__slider-text {
        display: flex;
        z-index: -1;
        position: absolute;
        height: 100%;
        width: 100%;
    }

    &__month {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;

        &:not(:first-child) {
            box-shadow: -0.02rem 0 0.05rem black;
        }
    }

    &__month--disabled {
        color: $text-disabled-color;
        background-color: $disabled-background-color
    }
}
